* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
 body {
   background-color: #171616;
     font-family: Lato, sans-serif;
     font-weight: 350;
 }
h1, h2, h3, h4, h5, h6, p {
    margin-top: 0;
    margin-bottom: 0.5rem;
    font-weight: 500;
    line-height: 1.2;
    color: white;
}

body p {
    color: #888;
    font-family: Lato, sans-serif;
    font-size: 16px;
    line-height: 1.6;
    margin: 0 0 15px;
    padding: 0;
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}